a {
  text-decoration: none;
}

.footer-contact {
  max-width: 100%;
  height: 276px;
  background: #F24B6A;
  position:relative;
}

.contact-container {
  display: flex;
  justify-content: space-evenly;
  align-items: baseline;
  align-content: center;
  width:90%;
  height: 100%;
  margin: 0 auto;
}

.footer-addresse {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  padding-top: 20px;
  line-height: 3;
}

.footer-addresse a {
  margin-bottom: 5px;
}


.link-column {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-top: 27px;
}

.column-one {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-top: 20px;
}


.link-column a {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  line-height: 235%;
  color: #FFFFFF;
}

.link-column img {
  margin-bottom: 0px;
}

.follow-us {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 100%;
  color: #FFFFFF;
}

